import { lazy, Suspense } from 'react'
import { BrowserRouter as Router, Route, Routes } from "react-router-dom";

import AppHeader from "../appHeader/AppHeader";
import Spinner from '../spinner/Spinner';

const Page404 = lazy(() => import('../pages/404'))
const MainPage = lazy(() => import('../pages/MainPage'))
const ComicsPage = lazy(() => import('../pages/ComicsPage'))
const SingleComicLayout = lazy(() => import('../pages/singleComicLayout/SingleComicLayout'));
const SingleCharacterLayout = lazy(() => import('../pages/singleCharacterLayout/SingleCharacterLayout'));
const SingleComicPage = lazy(() => import('../pages/SingleComicPage'))
const SinglePage = lazy(() => import('../pages/SinglePage'));

const App = () => {

    return (
        <Router>
            <div className="app">
                <AppHeader/>
                <main>
                    <Suspense fallback={<Spinner/>}>
                        <Routes>
                            <Route path='Marvel' element={<MainPage/>}/>

                            <Route path='Marvel/comics' element={<ComicsPage/>}/>
                           

                            <Route path='Marvel/comics/:comicId' element={<SingleComicPage/>} dataType='comics'/>

                            <Route path='Marvel/comics/:comicId' element={<SingleComicPage Component={SingleComicLayout}/>}>
                            </Route>

                            {/* <Route path='Marvel/comics/:comicId'>
                                <SingleComicPage Component={SingleComicLayout}/>
                            </Route>

                            <Route path="Marvel/characters/:id">
                                <SinglePage Component={SingleCharacterLayout} dataType='character'/>
                            </Route> */}

                            <Route path="Marvel/characters/:id" element={<SinglePage Component={SingleCharacterLayout} dataType='character'/>}></Route>

                            <Route path="*" element={<Page404/>}/> 
                        </Routes>
                    </Suspense>
                </main>
            </div>
        </Router>
    )
}

export default App;